<template>
  <div>
    <h2>Button Style</h2>
    <div class="align-h">
   <!-- 1-1 -->
      <button class="qr-button2 gradient-hover-button">Hover Me2</button>
      <button class="qr-button neon-glow-button">Click Me</button>
      <button class="qr-button shiny-glass-button">Discover More</button>
      <button class="qr-button animated-gradient-border-button">Submit</button>
      <button class="qr-button glitch-effect-button">Click Here</button>

    </div>
  </div>
</template>
<style scoped>
/* @import '../qrcss/css/button.css' */
/* Common styles for qr-button */
.qr-button {
  padding: 15px 30px;
  font-size: 16px;
  color: #fff;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

/* Gradient Hover Button */
.gradient-hover-button {
  background: linear-gradient(45deg, #ff00cc, #3333ff);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.gradient-hover-button:hover {
  background: linear-gradient(45deg, #ff3399, #3366ff);
}

/* Neon Glow Button */
.neon-glow-button {
  background-color: #4CAF50;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.neon-glow-button::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background-color: #4CAF50;
  z-index: -1;
  filter: blur(30px);
  transition: transform 0.5s ease;
}

.neon-glow-button:hover::before {
  transform: scale(1.2);
}

.neon-glow-button:hover {
  color: #4CAF50;
}

/* Shiny Glass Button */
.shiny-glass-button {
  background-color: #3498db;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

.shiny-glass-button::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.shiny-glass-button:hover::before {
  opacity: 1;
}

.shiny-glass-button:hover {
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.3);
  transform: translateY(-3px);
}

/* Animated Gradient Border Button */
.animated-gradient-border-button {
  background-color: transparent;
  border: 2px solid transparent;
  border-image: linear-gradient(45deg, #ff00cc, #3333ff) 1;
  border-image-slice: 1;
}

.animated-gradient-border-button:hover {
  border-image: linear-gradient(45deg, #ff3399, #3366ff) 1;
}

/* Glitch Effect Button */
.glitch-effect-button {
  background-color: #e74c3c;
  position: relative;
  overflow: hidden;
}

.glitch-effect-button::before,
.glitch-effect-button::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  clip: rect(0, 1000px, 0, 0);
  transition: clip 0.5s ease;
}

.glitch-effect-button:hover::before {
  clip: rect(50px, 1000px, 50px, 0);
}

.glitch-effect-button:hover::after {
  clip: rect(0, 1000px, 0, 0);
}

.glitch-effect-button:hover {
  transform: translateY(-3px);
}

</style>
